.container{
    display: flex;
    flex-direction: column;
    margin: auto; 
    margin-top: 200px;
    width: 600px;
    background:#e8e8e8e3; 
    padding-bottom: 40px;
}

.container2{
    display: flex;
    flex-direction: column;
    margin: auto; 
    margin-top: 200px;
    width: 600px;
    background:#839b8ee3; 
    padding-bottom: 40px;
}

.header {
    display: flex;
    flex-direction: column; 
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
}
.text{
    color: #02410d;
    font-size: 43px;
    font-weight: 750;
}

.underline{ 
    width: 70px;
    height: 5px; 
    background:#02410d;
    border-radius: 10px;
}

.inputs{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background: #eaeaea;
    border-radius: 10px;
}

.input img {
    margin: 0px 40px;
}

.input input{
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;
}

.forgot-password{
    padding-left: 62px;
    margin-top: 27px;
    color: #797979;
    font-size: 18px;
}

.forgot-password span{
    color: #034926;
    cursor: pointer;
}

.submit-container{
    display: flex;
    align-items: center;
    
}

.submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 59px;
    color: #fff;
    background-color: #18411f;
    font-size: 19px;
    font-weight: 700px;
    cursor: pointer;
    border-radius: 0px;
}

.submit_api{
    margin-top: 40px;
    align-self: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35%;
    height: 59px;
    color: #fff;
    background-color: #003708;
    font-size: 19px;
    font-weight: 700px;
    cursor: pointer;
    border-radius: 10px;
}

.gray{
    background: #eaeaea;
    color: #676767;
}